.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;
    padding-top: 146px;
    margin-left: -8px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;
    width: 500px;
    padding: 40px;
    background-color: #00b3ff;
    border-radius: 3px;
    padding-top: 37px;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close {
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1;
    width: 40px;
    height: 40px;
    padding: 0;
    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}

.modal__title {
  font-family: "Roboto Condensed", "Arail", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  color: #ffffff;
  margin: 0;
  text-align: left;
  margin-bottom: 4px;
}

.modal__description {
  margin: 0;
  text-align: left;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin-bottom: 38px;
  margin-right: 20px;
}

.modal__close-img {
  fill: #ffffff;
  width: 20px;
  height: 20px;
}
